import { Component } from '@angular/core';
import * as $ from 'jquery';
import { SearchService } from './search.service';
import { Subject } from 'rxjs/Subject';
import { AuthService } from '../auth.service';


@Component({
    selector: 'app-home',
    templateUrl: './home.component.html',
    styleUrls: ['./home.component.css'],
    providers: [SearchService, AuthService]
})
export class HomeComponent {

    results: Object;
    searchTerm$ = new Subject<string>();

    constructor(private searchService: SearchService,
               public authService: AuthService) {
        this.searchService.search(this.searchTerm$)
            .subscribe(results => {
            this.results = results.results;
            console.log(results);
            // If Search box contains no text slide header down
            if ($('.search-box').val() === '') {
                $('.welcome-row').slideDown(400);
                let resultsLength = $('li.main-results').length;
                $('.results-title').text('Results: ' + resultsLength);
            // Else hide the welcome header
            } else {
                $('.welcome-row').slideUp(400);
                let resultsLength = $('li.main-results').length;
                $('.results-title').text('Results: ' + resultsLength);
            }
        });
    }
      copyText(el: string) {
         $(el).siblings('.api-txt').select();
          document.execCommand('copy');
    }
    copyWithScript(el: string) {
        const apiUrl = $(el).siblings('.api-script-txt').select();
        document.execCommand('copy');
    }
    showMore() {
        $('.more-list').removeClass('display-none');

        $('body, html').animate({
          scrollTop: $($('.full-list')).offset().top
        }, 300);
        const currentResults: number = $('.more-list').length + 1;
        console.log(currentResults);
        $('.more-list').slice(currentResults).hide();
    }
    onScroll() {
        if ($('body').scrollTop() > 5000) {
            const currentResults: number = parseInt($('ul li').length);
            console.log('current list: ' + currentResults);
            const nextResults: number = currentResults / 4;
            $('.more-list').slice(nextResults).show();
            console.log('final results:' + nextResults);
        }
    }
//    keyPress(event: KeyboardEvent) {
//        const val = $('.search-box').val().toLowerCase();
//        if ($('.search-box').val() === '') {
//            $('.welcome-row').slideDown('slow');
//        } else {
//            $('.welcome-row').slideUp('slow');
//        }
//    }
}



